.profile__add{width:150px;height:50px;border:2px solid #fff;box-sizing:border-box;border-radius:2px;background-color:#000;background-image:url(./images/button-add.svg);background-repeat:no-repeat;background-position:50%;cursor:pointer;transition:opacity .15s linear}.profile__add:hover{opacity:.6}.profile__add:focus{outline:none}.profile__add:active{opacity:1;transition:0s}@media screen and (max-width:657px){.profile__add{width:282px;height:50px;margin-top:36px}}